/*I am not applying the hashes to these for testing purposes. 
I couldn't figure out how to get jest to work with css modules, 
and I didn't want to spend too much time trying to figure it out.
*/
:global(.task) {
  position: relative;
  max-width: 300px;
  margin: auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  background-color: white; }

:global(.pastDue) {
  position: relative;
  max-width: 300px;
  margin: auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  background-color: #f71e1e94; }

:global(.dueToday) {
  position: relative;
  max-width: 300px;
  margin: auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  background-color: #ffff006e; }

:global(.dueLater) {
  position: relative;
  max-width: 300px;
  margin: auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  background-color: #00800080; }

.checkbox {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 30%; }

.clickable {
  display: inline-block;
  padding: 0 10px 0 10px;
  width: 100%; }
  .clickable:hover {
    cursor: pointer; }

.name {
  display: block; }
  .name:hover {
    cursor: pointer; }

.due {
  display: inline-block;
  width: 120px; }
  .due:hover {
    cursor: pointer; }

.remove {
  display: inline-block;
  position: absolute;
  right: 0; }
  .remove:hover {
    cursor: pointer; }



